<template>
	<div id="spotList">
		<div id="spotListBox">
			<div id="spotListItem" v-proportion='0.3' v-for="(item,index) in spotResult" :key="index" @click="click(index)">
				<div id="spotNone" v-proportion='0.15'></div>
				<div id="spotListAll" v-proportion='0.3'>
					<img id="spotImgOne" :src="item.img1"/>
					<div id="spotListContent">
						<div id="spotContentWord" v-proportion='0.25'>
							<div id="spotWordName">{{item.name}}</div>
							<div id="spotWordInt">{{item.summary}}</div>
						</div>
						<div id='spotContentImg' v-proportion='0.25'>
							<img id="spotImgTwo"  :src="item.img2"/>
							<img id="spotImgThree"  :src="item.img3" />
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default{
	name : 'spotList',
	props:['spotResult'],
	methods:{
		click(index){
			this.$router.push({path:"spotDetail",query:{datas:this.spotResult[index]}})
		}
	}
}
</script>

<style>
	#spotList,#spotListBox,#spotListContent,#spotListAll,#spotContentWord,#spotContentImg{
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}
	#spotList{
		width: 100%;
		height: 100%;
	}
	#spotListBox{
		width: 72%;
		justify-content: space-between;
	}
	#spotListItem{
		width: 100%;
		margin: 1% 0 2% 0;
		border: 0;
		background-color: #E0F8F7;
		background:linear-gradient(-45deg,transparent 20%, paleturquoise 0);
		border-radius: 10%;
		position: relative;
	}
	#spotNone{
		position: absolute;
		top: 0;
		left: 0;
		background-color: white;
		width: 80%;
		float: left;
		z-index: 1;
	}
	#spotListAll{
		position: absolute;
		width: 90%;
		background-color: white;
		box-shadow: 0 0 50px 0px paleturquoise;
		position: relative;
		top: 7%;
		left: 5%;
		flex-direction: row;
		z-index: 2;
		-webkit-border-bottom-left-radius: 5%;
	}
	#spotImgOne{
		width: 40%;
		height: 100%;
		-webkit-border-bottom-left-radius: 5%;
	}
	#spotListContent{
		width: 60%;
		height: 100%;
		justify-content: space-between;
	}
	#spotContentWord{
		width: 100%;
		justify-content: space-around;
	}
	#spotWordName{
		font-size: 25px;
	}
	#spotContentImg{
		width: 100%;
		flex-direction: row;
	}
	#spotImgTwo,#spotImgThree{
		height: 100%;
		width: 50%;
	}
</style>
